.item {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 10px;
  border-radius: 4px;
  border: 1px solid #7b2f3d;
  box-shadow: 0 8px 24px rgb(0 0 0 / 50%);
  background-color: #cfcf5e;

  .status {
    display: block;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: #f72500;

    &.online {
      background-color: #07e956;
    }
  }

  .imgWrapper {
    width: 60px;
    height: 60px;
    border-radius: 4px;
    background-color: #778d45;

    display: flex;
    align-items: center;
    justify-content: center;
  }

  .name {
    font-size: 16px;
    font-weight: 500;
  }
}
